import { defineDocumentType, makeSource } from "contentlayer/source-files";

/** @type {import("contentlayer/source-files").ComputedFields} */
const computedFields = {
  slug: {
    type: "string",
    resolve: (doc) => `/${doc._raw.flattenedPath}`,
  },
  slugAsParams: {
    type: "string",
    resolve: (doc) => doc._raw.flattenedPath.split("/").slice(1).join("/"),
  },
};

export const Doc = defineDocumentType(() => ({
  name: "Doc",
  filePathPattern: "project/**/*.mdx",
  contentType: "mdx",
  fields: {
    homePage: {
      type: "boolean",
      required: true,
    },
    deprecated: {
      type: "boolean",
    },
    projectName: {
      type: "string",
      required: true,
    },
    description: {
      type: "string",
    },
    videoPath: {
      type: "string",
      required: true,
    },
    path: {
      type: "string",
      required: true,
    },
    mobile: {
      type: "boolean",
    },
    projectStyle: {
      type: "string",
      required: true,
    },
    imagePath: {
      type: "string",
    },
    bg1: {
      type: "string",
      required: true,
    },
    bg2: {
      type: "string",
      required: true,
    },
    rotation: {
      type: "level" | "rotate45Right" | "rotate45Left",
      required: true,
    },
    bg3: {
      type: "string",
      required: true,
    },
    order: {
      type: "number",
      required: true,
    },
    lineSpacing: {
      type: "string",
      required: true,
    },
  },
  computedFields,
}));

export default makeSource({
  contentDirPath: "src/content",
  documentTypes: [Doc],
});
